 .location__details {
    margin: 15px 0;
    font-size: x-large;
  }

  .location__details div {
    margin-bottom: 10px;
  }

  .location__map-icon-box {
    padding: 5px 0;
    margin-bottom: -10px;
    width: 340px;
    display: flex;
    justify-content: space-around;
    background-color: rgb(250, 227, 231);
  }

  .location__map-icon {
    width: 30px;
  }

  .location__map-item:hover {
    cursor: pointer;
    background-color: rgb(247, 195, 204);
  }

 .location__map-item {
  display: flex;
  align-items: center;
 } 

 .location__map-item span {
    margin-left: 10px;
    font-size: large;
 }

  .location__map {
    width: 340px;
    height: 340px;
  }

  .location__info {
    width: 310px;
    text-align: left;
    font-size: x-large;
    margin-top: 25px;
  }

  .location__info div {
    margin-bottom: 7px;
    font-size: 20px;
  }

  .info-item {
  display: flex;
  align-items: center;
  margin-bottom: 7px;
  font-weight: bold;
}

.info-item .info-icon-car {
  width: 25px;   /* 아이콘 작게 */
  height: 25px;
  margin-right: 5px;
  padding-top: 3px;
}

.info-item .info-icon {
  width: 23px;   /* 아이콘 작게 */
  height: 23px;
  margin-right: 5px;
  padding-top: 3px;
}

.info-icon-map {
  width: 23px; /* 클릭 영역 확보를 위해 기존 20px에서 25px로 키움 */
  height: 23px;
  cursor: pointer;
  margin-left: 7px;
}

.info-detail {
  margin-left: 10px;  /* ← 들여쓰기 */
}

.info-detail-desc {
  margin-left: 3px;  /* ← 들여쓰기 */
  /* white-space: nowrap; */
  margin-right: 3px;  /* ← 들여쓰기 */

}

  .display-flex-align-center {
    display: flex;
    align-items: left; /* 세로 중앙 정렬 */
}


.grid-box {
  width: 300px;         /*  전체 너비 */
  height: 50px;         /* 전체 높이 */
  display: grid;
  grid-template-rows: 1fr 1fr;   /* 위 1칸 / 아래 1칸 */
  grid-template-columns: 0.7fr 0.3fr;/* 아래 2칸 */
}

.grid-top {
  grid-column: 1 / 3;     /* 위 칸은 두 열 합치기 */
}

.grid-bottom-left {
  margin-left: 3px;
}

.grid-bottom-right {
  margin-left: -12px;
}
