.cover__container {
  position: relative;
  height: 100vh;
  /* width: 100%; */
  height: auto; /* 화면 전체 높이 */
  width: 100vw;
  overflow: hidden;
  position: relative;
}

.cover__container img {
  width: 100%;
  height: 100%;
  object-fit: cover;          /* 화면 꽉 차게, 비율 유지 */
  display: block;             /* inline 여백 제거 */
  backface-visibility: hidden; /* 렌더링 깜빡임 방지 */
  transform: translateZ(0);    /* GPU 가속으로 스크롤 안정화 */
}

.cover__main-photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

  .cover__overlay {
  position: absolute;
  bottom: 30px;        /* 화면 바닥에서 30px 위에 위치 */
  left: 50%;
  transform: translateX(-50%); /* 수평 중앙 정렬 */
  text-align: center;
  color: white;
  z-index: 2;
}

  .cover__person {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: xx-large;
    font-weight: bold;
    margin-bottom: 10px;
    white-space: nowrap;
  }
  
  @keyframes heartbeat {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.3);
    }
    100% {
      transform: scale(1);
    }
  }
  
  .cover__icon-heart {
    animation: heartbeat 1s infinite; 
    margin: 0 15px;
    color: rgb(252, 99, 188);
  }
  
  .cover__date {
    margin-top: 8px;
    font-size: x-large;
    white-space: nowrap;
  }

  .cover__place {
    margin-top: 8px;
    font-size: x-large;
    white-space: nowrap;
  }
  
  @keyframes playAnimation {
    0% {
      width: 0
    }
    100% {
      width: 100%;
    }
  }
  
  .cover__line {
    border-radius: 2px;
    margin-top: 30px;
    width: 340px;
    height: 4px;
    background: linear-gradient(to right, rgb(170, 138, 138), rgb(170, 138, 138)); /* 시작 색상부터 끝 색상까지의 그라데이션 설정 */
  }
  
  .cover__line::after {
    border-radius: 2px;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgb(95, 25, 25);;
    animation: playAnimation 20s infinite; /* 애니메이션 지속 시간 및 무한 반복 설정 */
  }
  
  .cover__icon-box {
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 300px;
  }

  .cover__music-btn:hover,
  .cover__music-btn:focus {
    cursor: pointer;
    color:rgb(214, 69, 69);
  }
